import React,{useState} from "react"


const AddCommentForm = ({articleName,setArticleInfo}) => {
    const [username,SetUsername] = useState('')
    const [commentText,SetCommentText] = useState('')

    const addComment = async () =>{
        const result= await fetch(`/api/article/${articleName}/addcomment`,{
            method:'post',
            body: JSON.stringify({username,comments:commentText}),
            headers:{
                'Content-Type': 'application/json',
            }
        })
        const body = await result.json();
        setArticleInfo(body);
        SetUsername('');
        SetCommentText('');
    }

    return(
            <div className=" justify-content-center">
                <h3>Add a Comment</h3>
                <div className="form-group">
                    <label className="sr-only" htmlFor="name">Name</label>
                    <input type="email" 
                            id="name" 
                            name="name" 
                            className="form-control mr-md-1" 
                            value={username}
                            placeholder="Name"
                            onChange={(event)=>SetUsername(event.target.value)}
                    />  
                    <br/>
                    <label className="sr-only" htmlFor="comment">Comment</label>
                    <textarea rows="4" 
                              cols="15" 
                              className="form-control mr-md-1" 
                              placeholder="Comment"

                              value={commentText}
                              onChange={(event)=> { SetCommentText(event.target.value)}}
                              />
                    </div>
                    <button className="btn btn-primary" onClick={()=>addComment()}>Add Comment</button>
                </div>
         
    )
}
 

export default AddCommentForm