import React,{useState,useEffect} from "react"
import articleContent from "./article-content"
import ArticleList from "../components/ArticleList"
import NotFoundPage from './NotFoundPage'
import CommentsList from "../components/CommentsList"
import LikeSection from "../components/LikeSection"
import AddCommentForm from "../components/AddCommentForm"


const Article = ({match}) =>  {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    const [articleInfo,setArticleInfo] = useState({upvotes:0,comments:[]})

    useEffect(()=>{

        const fetchData = async () => {
            const result = await fetch(`/api/article/${name}`)
            const body =  await result.json()
            setArticleInfo(body)
        }
        fetchData()
        
    },[name])

    if(!article) return <NotFoundPage/>
  
    const otherArticles = articleContent.filter(article=>article.name !== name)

    return(
    <>  
	    <article className="blog-post px-3 py-5 p-md-5">
		    <div className="container">
			    <header className="blog-post-header">
                    <h2 className="title mb-2">{article.title} </h2>
    <div className="meta mb-3"><span className="time">{article.readingTime} min read</span><span className="date">Liked by {articleInfo.upvotes}</span>
                        <span className="comment">{articleInfo.comments.length} comments</span></div>
                </header>
			    
			    <div className="blog-post-body">
                <figure className="blog-banner">
				       <img className="img-fluid" src={`/assets/images/blog/blog-post-${name}.jpg`} alt="blog-post-img"/>
			     </figure>

                    {article.content.map( (data,key) => (
                        <p key={key}> {data} </p>
                         )
                    )}	
			    </div>
			</div>	
            <LikeSection articleName={name} setArticleInfo={setArticleInfo}/>
    
	        </article>
            
            <section className="blog-list theme-bg-light py-5">
		        <div className="container">

                    <CommentsList comments={articleInfo.comments} />
                    <AddCommentForm articleName={name} setArticleInfo={setArticleInfo}/>
                    <h2 className="title text-center">Other Articles</h2><hr/>
                    <ArticleList articles={otherArticles} />
		        </div>
	    </section>

            
    </>
)}

export default Article