import React from "react"
import {Link} from "react-router-dom"

const Header = () =>  (
	<header className="header text-center">	    
		<h1 className="blog-name pt-lg-4 mb-0">
			<Link to="/">Abhisha's Blog</Link>
		</h1>
	
	<nav className="navbar navbar-expand-lg navbar-dark" >
	   
		<button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navigation" aria-controls="navigation" aria-expanded="false" aria-label="Toggle navigation">
		<span className="navbar-toggler-icon"></span>
		</button>
		<div id="navigation" className="collapse navbar-collapse flex-column" >
		<div className="profile-section pt-3 pt-lg-0">
				    <img className="profile-image mb-3 rounded-circle mx-auto" src="/assets/images/profile.png" alt="profile-img" />			
			        <hr/> 
				</div>
			<ul className="navbar-nav flex-column text-left">
				<li className="nav-item active">
					<Link className="nav-link" to="/"><i className="fas fa-home fa-fw mr-2"></i>
					Blog Home <span className="sr-only">(current)</span></Link>
				</li>
				<li className="nav-item">
					<Link className="nav-link" to="/article">
						<i className="fas fa-bookmark fa-fw mr-2"></i>Blog Post</Link>
				</li>
				<li className="nav-item">
					<Link className="nav-link" to="/about">
						<i className="fas fa-user fa-fw mr-2"></i>About</Link>
				</li>
			</ul>
			<div className="my-2 my-md-3">
				    <a className="btn btn-primary" href="mailto:abhisha5993@gmail.com" target="_blank" rel="noopener noreferrer">Get in Touch</a>
				</div>
		
		</div>
	</nav>
</header>
		
    
)

export default Header