import React from "react"
import {Link} from "react-router-dom"

const ArticleList = ({articles}) => (
 <>
    { articles.map((article,key)=> (
        <div className="item mb-5" key={key}>
            <div className="media">
            <img className="mr-3 img-fluid post-thumb d-none d-md-flex" src={`/assets/images/blog/blog-post-${article.name}.jpg`} alt="blog-post-img"/>
                <div className="media-body">
                    <Link to={`/article/${article.name}`}>
                        <h3 className="title mb-1">{article.title}</h3>
                    </Link>
                    <div className="intro">
                        {article.content[0].substring(0,200)}
                    </div>
                    <Link className="more-link" to={`/article/${article.name}`}>
                        Read More &rarr;
                    </Link>
                </div>
            </div>                            
        </div>
     ))
    }
</>
)

export default ArticleList