import React from "react"


const UpvoteSection = ({articleName,setArticleInfo}) => {

    const setUpvote = async() => {
       
        const result = await fetch(`/api/article/${articleName}/upvote`,
                     {method:'post'})
        const body = await result.json()
        setArticleInfo(body);
    }
    return(
        <div className="form-inline justify-content-center">
            <button className="btn btn-primary" onClick={()=>setUpvote()}> <i className="fas fa-thumbs-up  mr-2"></i>Like</button>    
        </div>
    )
}
 

export default UpvoteSection